




















































































































































  const COUNTSECEND = 60
  
	export default {
		data() {
			return {
				bankCode: '',
				bankInfo: null,
        username: '',
        applyId: '',
        idcard: '',
        phone: '',
        phonecode: '', // 填了手机号码之后，发送验证码
        phonecodeOnly: '', // 直接触发发送二维码
        piccode: '',
        codeImg: '',
        hasSendCode: false, // 填了手机号码之后，发送验证码
        hasSendCode2: false, // 直接触发发送二维码
        isClickCode: false, // 填了手机号码之后，发送验证码
        isClickCode2: false, // 直接触发发送二维码
        sending: false, // 填了手机号码之后，发送验证码
        sending2: false, // 直接触发发送二维码
        countNum: COUNTSECEND, // 填了手机号码之后，发送验证码
        countNum2: COUNTSECEND, // 直接触发发送二维码
        countTimer: null, // 填了手机号码之后，发送验证码
        countTimer2: null, // 直接触发发送二维码
        resultFlag: false,
        isSuccess: false,
        loading: false,
        resTitle: '查询结果',
        resArr: [],
        oldInfo: {
          username: null,
          idcard: null,
          phone: null
        },
        querying: false
			}
		},
    computed: {
      hasName() {
        let code = this.bankCode.toLowerCase()
        return code === 'hb' || code === 'ceb'
      },
      hasPhone() {
        let code = this.bankCode.toLowerCase()
        return code === 'boc' || code === 'hb' || code === 'citic' || code === 'pab' || code === 'cmb'
      },
      hasPhone2() {
        let code = this.bankCode.toLowerCase()
        return code === 'cgb'
      },
      onlyPhoneCode() {
        let code = this.bankCode.toLowerCase()
        return code === 'cmbc' || code === 'ceb' || code === 'bog'
      },
      hasPic() {
        let code = this.bankCode.toLowerCase()
        return code === 'bcm' || code === 'spd' || code === 'cmbc' || code === 'ceb' || code === 'cgb'
      },
      isEdit() {
        return !(this.oldInfo.username === this.username && this.oldInfo.idcard === this.idcard && this.oldInfo.phone === this.phone)
      }
    },
		onLoad(query) {
      this.bankCode = query.bankCode
      this.applyId = query.id
			this.$http({
        url: this.$apis.bankInfo,
        method: 'GET',
        data: {
          code: this.bankCode
        },
        cb: (err, data) => {
          if (!err && data) {
            this.bankInfo = data.data
          }
        }
      })
      // 如果是订单查询
      let queryInfo = uni.getStorageSync('queryInfo') ? JSON.parse(uni.getStorageSync('queryInfo')) : null
      if (queryInfo) {
        uni.removeStorageSync('queryInfo')
        this.oldInfo.username = queryInfo.name || null
        this.oldInfo.idcard = queryInfo.idcard || null
        this.oldInfo.phone = queryInfo.mobile || null
        this.username = queryInfo.name || ''
        this.idcard = queryInfo.idcard || ''
        this.phone = queryInfo.mobile || ''
      }
      // 如果有图形验证码，获取验证码
      if (this.hasPic) {
        this.getBankCode()
      }
      // 是否处于验证码获取倒计时状态
      if (uni.getStorageSync('QueryBankCodeStamp')) {
        let oldtime = uni.getStorageSync('QueryBankCodeStamp')
        let nowtime = new Date().getTime()
        let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
        if (cha < COUNTSECEND) {
          this.sending = true
          this.hasSendCode = true
          this.isClickCode = true
          this.countNum = COUNTSECEND - cha
          this.countTimer = setInterval(() => {
            this.countNum --
            if (this.countNum <= 0) {
              clearInterval(this.countTimer)
              this.countNum = COUNTSECEND
              this.sending = false
              this.isClickCode = false
            }
          }, 1000)
        }
      }
      if (uni.getStorageSync('QueryBankCodeStamp2')) {
        let oldtime = uni.getStorageSync('QueryBankCodeStamp2')
        let nowtime = new Date().getTime()
        let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
        if (cha < COUNTSECEND) {
          this.sending2 = true
          this.hasSendCode2 = true
          this.isClickCode2 = true
          this.countNum2 = COUNTSECEND - cha
          this.countTimer2 = setInterval(() => {
            this.countNum2 --
            if (this.countNum2 <= 0) {
              clearInterval(this.countTimer2)
              this.countNum2 = COUNTSECEND
              this.sending2 = false
              this.isClickCode2 = false
            }
          }, 1000)
        }
      }
		},
    methods: {
      getBankCode(id) {
        this.codeImg = ''
        if (this.hasPic) {
          this.$http({
            url: this.$apis.bankCode,
            method: 'GET',
            data: {
              bankCode: this.bankCode,
              uid: this.$store.state.userInfo.id
            },
            cb: (err, data) => {
              if (!err && data.code === 0) {
              	this.codeImg = data.data + '?' + Math.random()
              } else {
                uni.showToast({
                	title: data ? data.message : '获取图形验证码失败',
                  icon: 'none'
                })
              }
            }
          })
        } else {
          this.$http({
          	url: this.$apis.bankCode,
          	method: 'GET',
          	data: {
          		bankCode: 'SPD',
          		uid: this.$store.state.userInfo.id
          	},
          	cb: (err, data) => {
          		if (!err && data.code === 0) {
          			this.codeImg = data.data + '?' + Math.random()
          		} else {
          			uni.showToast({
          				title: data ? data.message : '获取图形验证码失败',
          				icon: 'none'
          			})
          		}
          	}
          })
        }
      },
      sendCode() {
        if (this.isClickCode) return
        // 如果是订单查询
        if (!this.isEdit) {
          if (this.hasPhone2 && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
              icon: 'none'
            })
            return
          }
          this.isClickCode = true
          if (!this.hasSendCode) this.hasSendCode = true
          this.$http({
            url: this.$apis.queryBank,
            method: 'GET',
            data: {
              bankCode: this.bankCode,
              uid: this.$store.state.userInfo.id,
              applyId: this.applyId,
              idCard: '',
              verifyCode: this.piccode,
              mobile: '',
              verifyMobileMsg: ''
            },
            cb: (err, data) => {
              if (!err && data.success) {
              	this.sending = true
                uni.showToast({
                	title: data && data.message ? data.message : '短信发送成功',
                  icon: 'none'
                })
              	this.countTimer = setInterval(() => {
              		this.countNum --
              		if (this.countNum <= 0) {
              			clearInterval(this.countTimer)
              			this.countNum = COUNTSECEND
              			this.sending = false
              			this.isClickCode = false
              		}
              	}, 1000)
              	// 保存发送时间
                uni.setStorageSync('QueryBankCodeStamp', (new Date().getTime()).toString())
              } else {
                uni.showToast({
                	title: data && data.message ? data.message : '短信发送失败',
                	icon: 'none'
                })
              	this.sending = false
              	this.isClickCode = false
              }
            }
          })
        } else {
          if (this.hasPhone2 && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
            	icon: 'none'
            })
            return
          }
          if (!this.$utils.validateIdCard(this.idcard)) {
            uni.showToast({
            	title: '请输入正确的身份证号码',
            	icon: 'none'
            })
          } else if (!this.$utils.validatePhone(this.phone)) {
            uni.showToast({
            	title: '请输入正确的手机号码',
            	icon: 'none'
            })
          } else {
            this.isClickCode = true
            if (!this.hasSendCode) this.hasSendCode = true
            this.$http({
              url: this.$apis.queryBank,
              method: 'GET',
              data: {
                bankCode: this.bankCode,
                uid: this.$store.state.userInfo.id,
                applyId: '',
                idCard: this.idcard,
                verifyCode: this.piccode,
                mobile: this.phone,
                verifyMobileMsg: ''
              },
              cb: (err, data) => {
                if (!err && data.success) {
                	this.sending = true
                  uni.showToast({
                  	title: data && data.message ? data.message : '短信发送成功',
                  	icon: 'none'
                  })
                	this.countTimer = setInterval(() => {
                		this.countNum --
                		if (this.countNum <= 0) {
                			clearInterval(this.countTimer)
                			this.countNum = COUNTSECEND
                			this.sending = false
                			this.isClickCode = false
                		}
                	}, 1000)
                	// 保存发送时间
                	uni.setStorageSync('QueryBankCodeStamp', (new Date().getTime()).toString())
                } else {
                  uni.showToast({
                  	title: data && data.message ? data.message : '短信发送失败',
                  	icon: 'none'
                  })
                	this.sending = false
                	this.isClickCode = false
                }
              }
            })
          }
        }
      },
      sendOnlyCode() {
        if (this.isClickCode2) return
        // 如果是订单查询
        if (!this.isEdit) {
          if (this.hasPic && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
            	icon: 'none'
            })
          } else {
            this.isClickCode2 = true
            if (!this.hasSendCode2) this.hasSendCode2 = true
            this.$http({
              url: this.$apis.queryBank,
              method: 'GET',
              data: {
                bankCode: this.bankCode,
                uid: this.$store.state.userInfo.id,
                applyId: this.applyId,
                idCard: '',
                verifyCode: this.piccode,
                mobile: this.phone,
                verifyMobileMsg: ''
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  uni.showToast({
                  	title: data.message,
                  	icon: 'none'
                  })
                  uni.removeStorageSync('QueryBankCodeStamp2')
                	this.sending2 = true
                	this.countTimer2 = setInterval(() => {
                		this.countNum2 --
                		if (this.countNum2 <= 0) {
                			clearInterval(this.countTimer2)
                			this.countNum2 = COUNTSECEND
                			this.sending2 = false
                			this.isClickCode2 = false
                		}
                	}, 1000)
                	// 保存发送时间
                  uni.setStorageSync('QueryBankCodeStamp2', (new Date().getTime()).toString())
                } else {
                  uni.showToast({
                  	title: data.message,
                  	icon: 'none'
                  })
                	if (this.hasPic) {
                		this.getBankCode()
                	}
                	this.hasSendCode2 = false
                	this.sending2 = false
                	this.isClickCode2 = false
                }
              }
            })
          }
        } else {
          if (!this.$utils.validateIdCard(this.idcard)) {
            uni.showToast({
            	title: '请输入正确的身份证号码',
            	icon: 'none'
            })
          } else if (this.hasPic && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
            	icon: 'none'
            })
          } else {
            this.isClickCode2 = true
            if (!this.hasSendCode2) this.hasSendCode2 = true
            this.$http({
              url: this.$apis.queryBank,
              method: 'GET',
              data: {
                bankCode: this.bankCode,
                uid: this.$store.state.userInfo.id,
                applyId: '',
                idCard: this.idcard,
                verifyCode: this.piccode,
                mobile: this.phone,
                verifyMobileMsg: ''
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  uni.showToast({
                  	title: data.message,
                  	icon: 'none'
                  })
                  uni.removeStorageSync('QueryBankCodeStamp2')
                	this.sending2 = true
                	this.countTimer2 = setInterval(() => {
                		this.countNum2 --
                		if (this.countNum2 <= 0) {
                			clearInterval(this.countTimer2)
                			this.countNum2 = COUNTSECEND
                			this.sending2 = false
                			this.isClickCode2 = false
                		}
                	}, 1000)
                	// 保存发送时间
                	uni.setStorageSync('QueryBankCodeStamp2', (new Date().getTime()).toString())
                } else {
                	uni.showToast({
                		title: data.message,
                		icon: 'none'
                	})
                	if (this.hasPic) {
                		this.getBankCode()
                	}
                	this.hasSendCode2 = false
                	this.sending2 = false
                	this.isClickCode2 = false
                }
              }
            })
          }
        }
      },
      clickQuery() {
        // 如果有订单id
        if (!this.isEdit) {
          if ((this.hasPhone || this.hasPhone2) && this.phonecode.length <= 0) {
            uni.showToast({
            	title: '请输入手机验证码',
            	icon: 'none'
            })
          } else if (this.hasPic && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
            	icon: 'none'
            })
          } else if (this.onlyPhoneCode && this.phonecodeOnly.length <= 0) {
            uni.showToast({
            	title: '请输入手机验证码',
            	icon: 'none'
            })
          } else {
            if (this.querying) return
            this.querying = true
            // this.loading = true
            uni.showLoading({
            	title: '正在查询...',
              mask: true
            })
            this.resArr = []
            if (this.bankCode.toLowerCase() === 'bcm' || this.bankCode.toLowerCase() === 'spd' || this.bankCode.toLowerCase() === 'cmbc' || this.bankCode.toLowerCase() === 'bosc' || this.bankCode.toLowerCase() === 'citic' || this.bankCode.toLowerCase() === 'cib' || this.bankCode.toLowerCase() === 'ceb' || this.bankCode.toLowerCase() === 'pab' || this.bankCode.toLowerCase() === 'bog' || this.bankCode.toLowerCase() === 'cmb' || this.bankCode.toLowerCase() === 'cgb') {
              let codeStr = ''
              if (this.onlyPhoneCode) {
                codeStr = this.phonecodeOnly
              } else {
                codeStr = this.phonecode
              }
              this.$http({
                url: this.$apis.queryBank,
                method: 'GET',
                data: {
                  bankCode: this.bankCode,
                  uid: this.$store.state.userInfo.id,
                  applyId: this.applyId,
                  idCard: '',
                  verifyCode: this.piccode,
                  mobile: '',
                  verifyMobileMsg: codeStr
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                  	if (data.data.length > 0) {
                  		uni.removeStorageSync('QueryBankCodeStamp')
                  		this.resArr = data.data
                      this.resArr.forEach(item => {
                        this.$set(item, 'resText', this.returnState(item.bank_apply_status))
                      })
                  		this.showResult(true)
                  		// 查询是否有资格升级VIP
                  		if (this.$store.state.userInfo.role >= 1) {
                        this.$http({
                          url: this.$apis.checkAgent,
                          method: 'POST',
                          data: {
                            uid: this.$store.state.userInfo.id
                          },
                          cb: (err, data) => {
                            if (!err && data.success) {
                            	this.$store.commit('changeAgentSuccess', true)
                            	if (data.data === 3) {
                            		this.$store.commit('changeAgentIndex', 1)
                            	} else if (data.data === 4) {
                            		this.$store.commit('changeAgentIndex', 1)
                            	}
                            }
                          }
                        })
                  		}
                  	} else {
                  		this.showResult(false)
                  	}
                  } else {
                    uni.showToast({
                    	title: data.message,
                    	icon: 'none'
                    })
                  }
                  if (this.hasPic) {
                  	this.getBankCode()
                  }
                  this.piccode = ''
                  this.phonecode = ''
                  this.phonecodeOnly = ''
                  this.querying = false
                  // this.loading = false
                  uni.hideLoading()
                  // this.hasSendCode = false
                  // this.hasSendCode2 = false
                }
              })
            } else {
              if (this.hasPic) {
                this.getBankCode()
              }
              this.piccode = ''
              this.phonecode = ''
              this.phonecodeOnly = ''
              this.querying = false
              // this.hasSendCode = false
              // this.hasSendCode2 = false
              setTimeout(() => {
                // this.loading = false
                uni.hideLoading()
                this.showResult(false)
              }, 1000)
            }
          }
        } else {
          if (this.hasName && !this.$utils.validateName(this.username)) {
            uni.showToast({
            	title: '请输入正确的姓名',
            	icon: 'none'
            })
          } else if (!this.$utils.validateIdCard(this.idcard)) {
            uni.showToast({
            	title: '请输入正确的身份证号码',
            	icon: 'none'
            })
          } else if ((this.hasPhone || this.hasPhone2) && !this.$utils.validatePhone(this.phone)) {
            uni.showToast({
            	title: '请输入正确的手机号码',
            	icon: 'none'
            })
          } else if ((this.hasPhone || this.hasPhone2) && !this.hasSendCode) {
            uni.showToast({
            	title: '验证码已失效，请重新发送',
            	icon: 'none'
            })
          } else if ((this.hasPhone || this.hasPhone2) && this.phonecode.length <= 0) {
            uni.showToast({
            	title: '请输入手机验证码',
            	icon: 'none'
            })
          } else if (this.hasPic && this.piccode.length <= 0) {
            uni.showToast({
            	title: '请输入图形验证码',
            	icon: 'none'
            })
          } else if (this.onlyPhoneCode && !this.hasSendCode2) {
            uni.showToast({
            	title: '验证码已失效，请重新发送',
            	icon: 'none'
            })
          } else if (this.onlyPhoneCode && this.phonecodeOnly.length <= 0) {
            uni.showToast({
            	title: '请输入手机验证码',
            	icon: 'none'
            })
          } else {
            if (this.querying) return
            this.querying = true
            // this.loading = true
            uni.showLoading({
            	title: '正在查询...',
              mask: true
            })
            this.resArr = []
            if (this.bankCode.toLowerCase() === 'bcm' || this.bankCode.toLowerCase() === 'spd' || this.bankCode.toLowerCase() === 'cmbc' || this.bankCode.toLowerCase() === 'bosc' || this.bankCode.toLowerCase() === 'citic' || this.bankCode.toLowerCase() === 'cib' || this.bankCode.toLowerCase() === 'ceb' || this.bankCode.toLowerCase() === 'pab' || this.bankCode.toLowerCase() === 'bog' || this.bankCode.toLowerCase() === 'cmb' || this.bankCode.toLowerCase() === 'cgb') {
              let codeStr = ''
              if (this.onlyPhoneCode) {
                codeStr = this.phonecodeOnly
              } else {
                codeStr = this.phonecode
              }
              this.$http({
                url: this.$apis.queryBank,
                method: 'GET',
                data: {
                  bankCode: this.bankCode,
                  uid: this.$store.state.userInfo.id,
                  applyId: '',
                  idCard: this.idcard,
                  verifyCode: this.piccode,
                  mobile: this.phone,
                  verifyMobileMsg: codeStr
                },
                cb: (err, data) => {
                  if (!err && data.success) {
                  	if (data.data.length > 0) {
                  		uni.removeStorageSync('QueryBankCodeStamp')
                  		this.resArr = data.data
                      this.resArr.forEach(item => {
                      	this.$set(item, 'resText', this.returnState(item.bank_apply_status))
                      })
                  		this.showResult(true)
                  		// 查询是否有资格升级VIP
                  		if (this.$store.state.role === 1) {
                  			this.$http({
                  				url: this.$apis.checkAgent,
                  				method: 'GET',
                  				data: {
                  					uid: this.$store.state.userInfo.id
                  				},
                  				cb: (err, data) => {
                  					if (!err && data.success) {
                  						this.$store.commit('changeAgentSuccess', true)
                  						if (data.data === 3) {
                  							this.$store.commit('changeAgentIndex', 1)
                  						} else if (data.data === 4) {
                  							this.$store.commit('changeAgentIndex', 1)
                  						}
                  					}
                  				}
                  			})
                  		}
                  	} else {
                  		this.showResult(false)
                  	}
                  } else {
                    uni.showToast({
                    	title: data.message,
                    	icon: 'none'
                    })
                  }
                  if (this.hasPic) {
                  	this.getBankCode()
                  }
                  this.piccode = ''
                  this.phonecode = ''
                  this.phonecodeOnly = ''
                  this.querying = false
                  // this.loading = false
                  uni.hideLoading()
                  // this.hasSendCode = false
                  // this.hasSendCode2 = false
                }
              })
            } else {
              if (this.hasPic) {
                this.getBankCode()
              }
              this.piccode = ''
              this.phonecode = ''
              this.phonecodeOnly = ''
              this.querying = false
              // this.hasSendCode = false
              // this.hasSendCode2 = false
              setTimeout(() => {
                // this.loading = false
                uni.hideLoading()
                this.showResult(false)
              }, 1000)
            }
          }
        }
      },
      showResult(type) {
        this.isSuccess = type
        this.resTitle = type ? '查询结果' : '未查询到申请记录'
        this.resultFlag = true
      },
      hideResult() {
        this.resultFlag = false
      },
      returnState(n) {
        let str = ''
        switch (n) {
          case 1:
            str = '审批中'
            break
          case 2:
            str = '已批卡'
            break
          case 3:
            str = '未通过'
            break
        }
        return str
      },
      gotoIndex() {
        uni.switchTab({
        	url: '/pages/shop/index'
        })
      }
    },
    watch: {
      idcard: function (val) {
        this.idcard = val.replace(/x/g, 'X')
      }
    }
	}
